.about {
   
    @extend %section;
    background-color: $footerBgColor;

   

    @media screen and (min-width: 1200px) {
        
        // height: 100%;
        
               
        background-image: url('/src/images/background1x.png');
        background-position: center;
        background-size: auto;
        background-repeat: no-repeat;
        @media screen and (min-device-pixel-ratio: 2),
        screen and(min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url('/src/images/background2x.png');
            }
    }
    @media screen and (max-width: 767px) {
        // height: 100%;
       
        background-image: url('/src/images/backgroundmob1x.png');
            background-position:50% 75%;
            background-size: contain;
            background-repeat: no-repeat;
        
            @media screen and (min-device-pixel-ratio: 2),
            screen and(min-resolution: 192dpi),
            screen and (min-resolution: 2dppx) {
            background-image: url('/src/images/backgroundmob2x.png');
            }
    }

&__container {
    @extend %container;
    }
    

&__subtitle {
        margin: 0;
        font-family: $font-secondary;
        font-style: normal;
        font-weight: 400;
        text-align: center;
        font-size: 18px;
        line-height: 1.16;
        text-transform: uppercase;
        
        color: $pinkTextColor;
        @media screen and (max-width: 767px) {
            margin-bottom: 15px;
            font-size: 18px;
            line-height: 1.16;
        }

        @media screen and (min-width: 768px) and (max-width: 1199px) {
            margin-bottom: 16px;
            font-size: 26px;
            line-height: 1.15;
        }

        @media screen and (min-width: 1200px) {
            margin-bottom: 16px;
            font-size: 30px;
            line-height: 1.13;
        }
    }

&__title {
        margin: 0;
        font-family: $font-secondary;
        font-style: normal;
        font-weight: 400;
       
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        
        color: $primmaryRedColor;
            
        @media screen and (max-width: 767px) {
                margin: 0 auto;
                margin-bottom: 32px;
                font-size: 34px;
                line-height: 1.15;
                
                max-width: 200px;
        }
            
        @media screen and (min-width: 768px) and (max-width: 1199px) {
                margin-bottom: 40px;
                font-size: 48px;
                line-height: 1.15;
                
        }
            
        @media screen and (min-width: 1200px) {
                margin-bottom: 60px;
                font-size: 58px;
                line-height: 1.13;
                letter-spacing: 0.06em;
               
        }
    }

&__wrap {
        @media screen and (max-width: 767px) {
            display: block;
        }
        @media screen and (min-width: 768px) {
                display: flex;
                justify-content: space-between;
                align-items:center
        }
        @media screen and (min-width: 1200px) {
            display: flex;
            justify-content: flex-start;
            align-items: center
        }
    }

&__imgwrap {
        @media screen and (max-width: 767px) {
                margin-left: auto;
                margin-right: auto;
            }
            
        @media screen and (min-width: 768px) and (max-width: 1199px) {
                              
            // margin-right: 32px;
        }
            
        @media screen and (min-width: 1200px) {
                margin-right: 99px;
        }
    }

&__cow {
        max-width: 100%;
        height: auto;
        display: block;
            
        
    }

    

&__first-text {
        margin: 0;
        max-width: 220px;
        font-family: $font-primary;
        font-style: normal;
        text-align: left;
        font-weight: 500;
        font-size: 12px;
        line-height: 1.58;
            
        margin-bottom: 40px;
        color: $primmaryBlackColor;
       
            
        @media screen and (min-width: 768px) and (max-width: 1199px) {
            max-width: 334px;
            text-align: start;
            font-size: 14px;
            line-height: 1.78;
            margin-bottom: 40px;
            }
            
        @media screen and (min-width: 1200px) {
            max-width: 463px;
            text-align: start;
          
            line-height: 1.78;
            font-size: 16px;
            line-height: 1.75;
            margin-bottom: 32px;
            }
    }

&__second-text {
            margin: 0;
            max-width: 220px;
            margin-bottom: 10px;
            font-family: $font-primary;
            font-style: normal;
            text-align: left;
            font-weight: 500;
            font-size: 12px;
            line-height: 1.58;
            margin-bottom: 12px;
            
            color: $greyTextColor;
           
            
            @media screen and (min-width: 768px) and (max-width: 1199px) {
                max-width: 334px;
                font-size: 14px;
                line-height: 1.76;
                letter-spacing: 0.04em;
            }
            
            @media screen and (min-width: 1200px) {
                max-width: 463px;
                margin-bottom: 30px;
                font-size: 14px;
                line-height: 1.93;
                letter-spacing: 0.04em;
        }
    }
    
&__third-text {
            margin: 0;
            max-width: 220px;
            font-family: $font-primary;
            font-style: normal;
            text-align: left;
            font-weight: 500;
            font-size: 12px;
            line-height: 1.58;
            
            margin-bottom: 20px;
            color: $greyTextColor;
            
           
            
            @media screen and (min-width: 768px) and (max-width: 1199px) {
                max-width: 334px;
                font-size: 14px;
                line-height: 1.76;
                letter-spacing: 0.04em;
                margin-bottom: 40px;
            }
            
            @media screen and (min-width: 1200px) {
                max-width: 463px;
                font-size: 14px;
                line-height: 1.93;
                letter-spacing: 0.04em;
                margin-bottom: 40px;
            }
    }

&__btn {
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: 176px;
            padding: 14px 37px;
            border: none;
            border-radius: 22px;
            font-family: $font-primary;
            font-weight: 700;
            font-size: 16px;
            line-height: 1.13;
            cursor: pointer;
            color: $primmaryRedColor;
            background: $primmaryWhiteColor;
            text-align: center;
            transition: background-color 400ms ease-in-out;
    
    
            &::after {
                content: "";
                display: block;
                width: 6px;
                height: 10px;
                background-image: url(/src/images/arrow.svg);
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                margin-left: 13px;
            }
    
            &:hover,
            &:focus,
            &:active {
                background-color: $accentBtnColor;
            }
        }
}


